
import { defineComponent } from "vue";
import { Router, useRouter } from 'vue-router';
export default defineComponent({
  name: "LayoutHeader",
  setup() {

    const router:Router = useRouter()
    const handleLoginOut =()=>{
      localStorage.clear();
      router.push('/login')
    }
    return{
      handleLoginOut
    }
  }
});
