
import {
  defineComponent,
  toRefs,
  ref,
  UnwrapRef,
  reactive,
  getCurrentInstance,
} from "vue";

import { Router, useRouter } from "vue-router";

interface IMenu {
  name: string;
  icon: string;
  path: string;
  component: string;
  children?: Array<{
    name: string;
    icon: string;
    path: string;
    component: string;
  }>;
}



interface IBreadcrumb {
  name: string;
  path: string;
}

interface IState {
  openKeys: string[];
  rootSubmenuKeys: string[];
  selectedKeys: string[];
  menu:IMenu[]
}

export default defineComponent({
  name: "LayoutSider",
  setup(props, context) {
    const menu = ref<IMenu[]>([
      {
        name: "数据集管理",
        icon: "",
        path: "/dataSet",
        component: "dataSet",
        children: [
          {
            name: "创建数据集",
            icon: "",
            path: "/dataSet/create",
            component: "",
          },
          {
            name: "数据集列表",
            icon: "",
            path: "/dataSet/list",
            component: "",
          },
          {
            name: "草稿箱",
            icon: "",
            path: "/dataSet/drafts",
            component: "",
          },
          {
            name: "服务审核",
            icon: "",
            path: "/dataSet/audit",
            component: "",
          },
        ],
      },
      {
        name: "配置管理",
        icon: "",
        path: "/config",
        component: "config",
        children: [
          {
            name: "业务标签",
            icon: "",
            path: "/config/normal",
            component: "",
          },
        ],
      },
    ]);
    const { proxy }  = getCurrentInstance() as any;
    const selected: string = proxy.$router.currentRoute && proxy.$router.currentRoute.value.path;

    const state: UnwrapRef<IState> = reactive({
      openKeys: ["/dataSet"],
      rootSubmenuKeys: ["/dataSet", "/config"],
      selectedKeys: [selected],
      menu:menu
    });

    // 获取路由器实例
    const router: Router = useRouter();

    // // 路由数据重新封装
    // const routerPackag = (routers: IMenu[]) => {
    //   routers.filter((itemRouter) => {
    //     if (itemRouter.component != "dataSet") {
    //     console.log('itemRouter',itemRouter) // 路由溢出
    //     router.addRoute("dataSet/create", {
    //       path: `${itemRouter.path}`,
    //       name: itemRouter.name,
    //       component: () => import(`@/${itemRouter.component}`),
    //     });
    //   }
    //     console.log('itemRouter',itemRouter)
    //     // 是否存在子集
    //     if (itemRouter.children && itemRouter.children.length) {
    //       routerPackag(itemRouter.children);
    //     }
    //     return true;
    //   });
    // };
    // routerPackag(state.menu);
    // 菜单点击事件
    const selectItem = (item: any) => {
      router.push({
        path: item.key,
      });

    };
    // 处理菜单展开 - 只展开一个菜单
    const onOpenChange = (openKeys: string[]) => {

      const latestOpenKey = openKeys.find(
        (key) => state.openKeys.indexOf(key) === -1
      );
      if (state.rootSubmenuKeys.indexOf(latestOpenKey!) === -1) {
        state.openKeys = openKeys;
      } else {
        state.openKeys = latestOpenKey ? [latestOpenKey] : [];
      }
    };

    return {
      selectItem,
      onOpenChange,
      ...toRefs(state),
    };
  },
});
