
import { reactive } from "vue";
import { Router,useRouter } from "vue-router";

export default {
  setup() {
    // 获取路由器实例
    const router:Router = useRouter();
    const state = reactive({
      type: String,
      default: "404",
    });
    const backHome = () => {
      router.push("/dataSet/create");
    };
    return {
      state,
      backHome,
    };
  },
};
